import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'
import {start, Start, rainbow, StyledText, Content, ContentGroup, Parent, Sub, Body, BodyImage, StyledLink, Glow} from './styles.js'

class HeliaMono extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover1: false,
      hover2: false,
      hover3: false,
      hover4: false
    }
  }

  mouseOver = (p) => {
    this.setState({[p]: true})
    console.log(this.state.hover)
  }

  mouseOut = (p) => {
    this.setState({[p]: false})
    console.log(this.state.hover)
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>
              <p>
                <Glow>
                Portraits
                </Glow>

                <Sub>
                Jun 2018 - Present
                </Sub>

                <Body>
                Portraits is an ongoing self portrait series that I began at the end of Summer in 2019.
                Essentially a glorified selfie gallery, I aim to capture myself at specific moments of self-decided significance in a variety of styles (anything from fashion photography to an actual iPhone selfie).
                </Body>

                <BodyImage>
                <img src={require('../assets/images/portraits1.jpg')} />
                <img src={require('../assets/images/portraits2.jpg')} />
                <img src={require('../assets/images/portraits3.jpg')} />
                <img src={require('../assets/images/portraits4.jpg')} />
                <img src={require('../assets/images/portraits5.jpg')} />
                <img src={require('../assets/images/portraits6.jpg')} />
                <img src={require('../assets/images/portraits7.jpg')} />
                <img src={require('../assets/images/portraits8.jpg')} />
                <img src={require('../assets/images/portraits9.jpg')} />
                <img src={require('../assets/images/portraits10.jpg')} />
                <img src={require('../assets/images/portraits11.jpg')} />
                <img src={require('../assets/images/portraits12.jpg')} />
                <img src={require('../assets/images/portraits13.jpg')} />
                <img src={require('../assets/images/portraits14.jpg')} />
                <img src={require('../assets/images/portraits15.jpg')} />
                <img src={require('../assets/images/portraits16.jpg')} />
                <img src={require('../assets/images/portraits17.jpg')} />
                </BodyImage>
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default HeliaMono



